@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0px;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.body-section {
    background: #ffffff;
    font-family: Roboto;
    font-style: normal;
    padding-right: 200px;
    padding-left: 200px;
    padding-top: 60px;
    padding-bottom: 60px;
    overflow: hidden;
}
.header {
  height: 500px;
  background: #0078e8;
  line-height: 100px;
  text-align: center;
  display: flex;
}

.header .title {
  text-align: center;
  color: white;
  margin: auto;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 50px;
  line-height: 88px;

  color: #ffffff;
}

.about {
  height: 75%;
  background: #f8f8f8;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 30px;
  padding-bottom: 30px;
}

.title {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 50px;
  line-height: 59px;


  /* text color */

  color: #333333;
}

.main-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  color: #828282;
  font-size: 30px;
}

.extra-text{
  font-size: 25px;
}

.about-link {
  color: #0078E8;
}

.homeLink img {
  width: 35px;
  height: 35px;
  position: relative;
  left: 40px;
  top: 12px;
  transition: transform 0.8s ease-in-out;
}

.homeLink img:hover {
  transform: rotate(360deg);
}

.home {
  display: flex;
}
.homeLink h1 {
  margin-left: 1.75rem;
  color: #fff;
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 550;
  font-size: larger;
  margin-top: 15px;
}
.navbar-nav .nav-link {
  line-height: 0px;
}

.links {
  line-height: 0px;
  padding: 0px;
}

.links p {
  margin: 20px;
  color: #fff;
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 550;
}

.links p:hover {
  color: #bbc1c8;
  transform: translateY(-0.25rem);
}

.navbar {
  z-index: 100;
  top: 0px;
  padding: 0px;
  position: relative;
  background-color: #0078e8 !important;
  width: 100%;
  justify-content: space-between;
}

.nav-link {
  color: white !important;
  font-family: CeraBold;
  font-size: 15px;
  letter-spacing: 0em;
  margin-right: 20px;
  line-height: 45px;
  text-align: center;
}

.navbar-collapse {
  flex-grow: unset !important;
}

@media (min-width: 992px) {
  .navbar-expand-lg .navbar-collapse {
  }
}

.project-section {
  background: #fff;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 30px;
  padding-bottom: 30px;
  overflow: hidden;
  transition: 200ms;
  position: relative;
}

.projects {
  display: flex;
  	flex-direction: row;
  	flex-wrap: wrap;
  	justify-content: space-evenly;
  	padding: 0 50px 25px;
    margin: 0 0 25px;
}



* {
  margin: 0;
  padding: 0;
  font-family: Roboto,Helvetica,sans-serif;
}

.footer {
  width: 100%;
  background: #2D3E50;
  display: block;
  padding-left: 5rem;
}

.inner-footer {
  width: 95%;
  margin: auto;
  padding: 1.875rem 0.625rem;
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: center;
}

.footer-items {
  width: 25%;
  padding: 0.625rem 2.5rem;
  box-sizing: border-box;
}

.footer-logo div{
  display: inline-block;
  color: #fff;
  font-family: Roboto,Helvetica,sans-serif;
}

.footer-logo img {
  width: 2.1875rem;
  height: 2.1875rem;

  top: 0.75rem;
  /*padding-left: 225px;*/
  position: relative;
  margin-right: 0rem;
}  

h1 {
  padding: 0rem 0;
  font-size: 1.125rem;
  color: #fff;
  display: inline-block;
  margin-left: 0.5rem;
}

.footer-items p {
  color: grey;
  font-size: 1rem;
  text-align: justify;
  line-height: 1.5625rem;
}

h2 {
  margin: 0.625rem 0;
  color: #fff;
  font-size: 1.25rem;
  font-weight: 900;
  text-shadow: none;
}

.border {
  height: 0.125rem;
  width: 2.5rem;
  background: #2D3E50;
}

ul {
  list-style: none;
  color: #BBC1C8;
  font-size: 0.9375rem;
  font-weight: lighter;
  letter-spacing: 0.03125rem;
}

ul a {
  text-decoration: none;
  outline: none;
  color: #BBC1C8;
  transition: 0.3s;
}

ul a:hover {
  color: #fff;
}

ul li {
  margin: 1.5625rem 0;
  height: 0rem;
}

li i {
  margin-right: 1rem;
  vertical-align: middle;
}

.fa-facebook {
  margin-left: 5px;
}

.footer-bottom {
  padding: 3.75rem;
  background: #2D3E50;
  color: #BBC1C8;
  font-size: 0.9375rem;
  margin-left: 6.25rem;
  font-weight: lighter;
}

@media screen and (max-width: 67.8125rem) {
  .footer-items {
      width: 50%;
  }
  .footer-logo {
    width: 50%;
  }
  .footer-bottom {
    width: 50%;
    margin-left: 0;
  }
  .footer {
    padding-left: 0;
  }
}

@media screen and (max-width: 37.5rem) {
  .footer-items {
      width: 100%;
  }
  .footer-logo {
    width: 100%;
    margin-left: 2rem;
  }
  .footer-bottom {
    width: 100%;
    margin-left: 0;
  }
  .footer {
    padding-left: 0;
  }
}
.header-section {
  height: 400px;
  background: #0078e8;
  font-family: Roboto;
  font-style: normal;
  padding: 30px 200px 30px 200px;
  overflow: hidden;
}

.header-section .title {
  text-align: left;
  color: #ffffff;
  font-size: 50px;
  line-height: 88px;
  padding-top: 130px;
  margin: 0px;
}

.header-section .info {
  text-align: left;
  color: #ffffff;
  font-size: 20px;
}

@media only screen and (max-width: 1000px) {
  .header-section {
    height: 400px;
    padding: 0 50px 30px 50px  
  }
}
.our-mission-section {
    height: 300px;
    background: #ffffff;
    font-family: Roboto;
    font-style: normal;
    padding: 60px 200px 30px 200px;
    overflow: hidden;
}

.our-mission-section .title {
    text-align: left;
    color: #000000;
    font-size: 50px;
    line-height: 88px;
    margin: 0px;
}

.our-mission-section .mission-statement {
    text-align: left;
    color: #000000;
    font-size: 20px;
}

.our-values-section {
    background: #f8f8f8;
    font-family: Roboto;
    font-style: normal;
    padding: 60px 200px 60px 200px;
    overflow: hidden;
}

.our-values-section .title {
    text-align: left;
    color: #000000;
    font-size: 50px;
    line-height: 88px;
    margin: 0px;
}

.value {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 30px;
}

.value-info {
    flex: 1 0 200px;
    align-items: center;
}

img {
    max-height: 100px;
    border-radius: 5px;
    margin-right: 20px;
}

img.icon {
    height: 300px;
}

.value-name {
    color: #0078e8;
    font-size: 35px;
    margin-bottom: 5px;
}

.description {
    color: darkgray;
    font-size: 18px;
}

.our-team-section {
    background: #ffffff;
    font-family: Roboto;
    font-style: normal;
    padding: 60px 200px 100px 200px;
    overflow: hidden;
}

.our-values-section .title {
    text-align: left;
    color: #000000;
    font-size: 50px;
    line-height: 88px;
    margin: 0px;
}

.members {
    display: flex;
    text-align: center;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

